import './telemetry.service';
import constants from '../common/constants';
import * as OTel from '@microsoft/oteljs';

export default function otelTelemetry($window) {

  this.trackEvent = function (eventName, properties, measurements) {
    if (!enabledSetting) {
      return;
    }
    let dataFields = [];

    //remove data prefix from session id and browser session id
    $window.otelLogger.partAFields = [
      OTel.makeStringDataField("Session.Id", properties['Session_Id']),
      OTel.makeStringDataField("BrowserSessionId", properties['BrowserSessionId']),
    ];

    for (let item in properties) {
      if(item !== 'AppInfo.Name' && item !== 'Session_Id' && item !== 'BrowserSessionId'){
        dataFields.push(OTel.makeStringDataField(item, properties[item]));
      }
    }
    for (let item in measurements) {
      dataFields.push(OTel.makeStringDataField(item, measurements[item]));
    }

    let otelEvent = {
      eventName: constants.TELEM_PREFIX + "." + eventName,
      dataFields: dataFields,
      eventFlags: {
        dataCategories: 2,
        diagnosticLevel: 10,
      },
    };

    //remove diagnostic for now per D&F guidelines, only log to Aria
    if(otelEvent['eventName'].toLowerCase().includes("diagnostic.")){
      return;
    }
    $window.otelLogger.sendTelemetryEvent(otelEvent);
  };

  this.trackTrace = function (message, properties) {
    if (!enabledSetting) {
        return;
    }
    let dataFields = [];

    //remove data prefix from session id and browser session id
    $window.otelLogger.partAFields = [
      OTel.makeStringDataField("Session.Id", properties['Session_Id']),
      OTel.makeStringDataField("BrowserSessionId", properties['BrowserSessionId']),
    ];

    for (let item in properties) {
      if(item !== 'AppInfo.Name' && item !== 'Session_Id' && item !== 'BrowserSessionId'){
        dataFields.push(OTel.makeStringDataField(item, properties[item]));
      }
    }
    let otelEvent = {
      eventName: constants.TELEM_PREFIX + "." + message,
      dataFields: dataFields,
      eventFlags: {
        dataCategories: 2,
        diagnosticLevel: 10,
      },
    };
    //set event name based on message because can't log to console with custom event name higher upstream
    //also remove diagnostic Info and Warn for now per D&F guidelines, only log to Aria
    if(message.toLowerCase() === "info"){
      otelEvent['eventName'] = constants.TELEM_PREFIX + ".Diagnostic.Info";
      return;
    } else if(message.toLowerCase() === "warn"){
      otelEvent['eventName'] = constants.TELEM_PREFIX + ".Diagnostic.Warn";
      return;
    } else if(message.toLowerCase() === 'error'){
      otelEvent['eventName'] = constants.TELEM_PREFIX + ".Diagnostic.Error";
    }

    $window.otelLogger.sendTelemetryEvent(otelEvent);
  };

  var enabledSetting = true;

  this.setEnabledSetting = function (enabled) {
    enabledSetting = enabled;
  };
}