import Utilities from '../common/utilities';
import { AWTLogManager } from '@aria/webjs-sdk';
import { TelemetryLogger } from '@microsoft/oteljs';
import { getDeviceInfo } from '@office-start/device-detection-utility';

//TODO: Rename this file to OTel.ts and remove Aria.ts

// Set instrumentation key depending on whether extension is in development mode
//[SuppressMessage("Microsoft.Security", "CS002:SecretInNextLine", Justification="not a auth secret. designates the Aria tenent")]
var tenantId = '2c8ba5f543dc45cabca6a8903ae65ec0-17b4b4b1-0680-4459-8c45-b7c284ec277d-7329';

//Initialize OTel Logger
//Don't attach sink yet, enable queue so events don't get dropped
const telemetryLogger = new TelemetryLogger(undefined, undefined, {
  // Enable queue, otherwise events will get dropped until the sink is attached
  enableQueue: true,
});
telemetryLogger.setTenantToken(
  "Office.Taos.M365Extension",
  "1ad73c205de94133974c390ca1abac47-17f7e2b2-1b01-44f9-8ff4-c8bc7ed524c7-6756"
);

(window as any).otelLogger = telemetryLogger;

//Gather and store device type
const deviceInfo = getDeviceInfo();
window.sessionStorage.setItem('deviceType', deviceInfo?.platform?.platformType);
//Create session id
var browserSessionId = Utilities.getNewId();
window.sessionStorage.setItem('browserSessionId', browserSessionId);

// Initialize
AWTLogManager.initialize(tenantId);
const browserVersion = Utilities.getBrowserAndVersion();
if (browserVersion) {
    AWTLogManager.setContext('DeviceInfo.BrowserName', browserVersion.browser);
    AWTLogManager.setContext('DeviceInfo.BrowserVersion', browserVersion.version);
}
// Store the logger instance in a variable that is accessible in other methods of your page
var ariaLogger = AWTLogManager.getLogger(tenantId);
(window as any).ariaLogger = ariaLogger;
